
@media (max-width: 1024px) {
.days li {
        margin-bottom: 2px
}
.days .card {
        margin: 2px!important;
}
}
.hovered {
    -webkit-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
}
.hovered:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}
.month {
    padding: 35px 25px;
    width: 100%;
    background: #1abc9c;
    text-align: center;
}


/* Month list */
.month ul {
    margin: 0;
    padding: 0;
}
.month ul li {
    color: white;
    font-size: 20px;
    text-transform: uppercase;
    letter-spacing: 3px;
}


/* Previous button inside month header */
.month .prev {
    float: left;
    padding-top: 10px;
}


/* Next button */
.month .next {
    float: right;
    padding-top: 10px;
}


/* Weekdays (Mon-Sun) */
.weekdays {
    margin: 0;
    padding: 10px 0;
    background-color: #ddd;
}
.weekdays li {
    display: inline-block;
    width: 13.6%;
    color: #666;
    text-align: center;
}


/* Days (1-31) */
.days {
    padding: 10px 0 0 30px;
    background: #eee;
    margin: 0;
    text-align: left;
}
.days li {
    list-style-type: none;
    display: inline-block;
    width: 13.6%;
    text-align: center;
    margin-bottom: 5px;
    font-size: 12px;
    color: #777;
}


/* Highlight the "current" day */
.days li .active {
    padding: 5px;
    background: #1abc9c;
    color: white !important
}


/* Add media queries for smaller screens */
@media screen and (max-width:720px) {
.weekdays li,
    .days li {
        width: 13.1%;
}
}
@media screen and (max-width: 420px) {
.weekdays li,
    .days li {
        width: 12.5%;
}
.days li .active {
        padding: 2px;
}
}
@media screen and (max-width: 290px) {
.weekdays li,
    .days li {
        width: 12.2%;
}
}



