
.logo-container {
  padding-top: 10vh;
  text-align: center;
}
.project-logo {
  font-size: 4rem;
  color: white;
  margin: 0 auto;
  display: block;
}
.background {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.background-1 {
  background-image: url(/static/images/in-Study-background-1.jpeg);
}
.background-2 {
  background-image: url(/static/images/in-Study-background-2.jpeg);
}
.background-3 {
  background-image: url(/static/images/in-Study-background-3.jpeg);
}
.background-4 {
  background-image: url(/static/images/in-Study-background-4.jpeg);
}
.bacground-muted {
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
}
.login-layout {
  padding-left: 30%;
  padding-right: 30%;
}
.rounded {
  border-radius: 5px;
  border: 1px solid white !important;
}
.workspace-logo {
  margin-top: 1vh !important;
  margin: 0 auto;
  display: block;
  visibility: hidden;
}
.visible-logo {
  visibility: visible;
}
.static-height {
  /* height: 70vh; */
}
.stepper-title {
  font-size: 2vh;
}
.header-border {
  border-bottom: 1px solid white;
}
@media (max-width: 1024px) {
.login-layout {
    padding-top: 10vh;
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 10px;
}
}



